
.app {
  height:100vh;
  display: flex;
  background-color:#020230;
  color: white;
}
.main{
  width: 75%;
  background:linear-gradient(to bottom,rgba(0,0,0,0),#020230),
   url("./assets/img.jpg")center;
   display: flex;
   flex-direction: column;
  }
.top{
 height: 50%;
 position: relative;
}
.timer{
  height: 70px;
  width: 70px;
  border-radius: 50%;
  border: 5px solid white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  font-weight: 700;
  position: absolute;
  bottom: 10px;
  left: 80px;
}
.bottom{
 height: 50%;

}
.trivia{
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

}
.question{
  width: 80%;
  background-color:linear-gradient(#100241,black);
  border: 2px solid white;
  text-align: center;
  padding: 20px;
  border-radius: 10px;
  font-size: 20px;
}
.answers{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.answer{
width: 40%;
padding: 10px;
text-align: center;
background-color:linear-gradient(#0e0124,#22074d);
border: 1px solid white;
border-radius: 15px;
font-size: 20px;
font-weight: 300;
margin: 0 10px 20px 10px;
cursor: pointer;

}
.answer:hover,.answer.active{
  background-color: mediumblue;
}

.answer.correct{
  animation: correct 3s ease forwards;
}
@keyframes correct{
  0%,
  22%,
  42% {
    background: mediumblue;
  }
  20%,
  40%,
  60% {
    background-color:linear-gradient(#100241,black);

  }
  62%,100%{
    background: green;
  }
}

.answer.wrong{
  animation: wrong 3s ease forwards;
}
@keyframes wrong{
  0%,
  22%,
  42% {
    background: mediumblue;
  }
  20%,
  40%,
  60% {
    background-color:linear-gradient(#100241,black);

  }
  62%,100%{
    background: red;
  }
}

.pyramid{
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.moneyList{
  list-style: none;
  width: 100%;
  padding: 20px;
}
.monyListItem{
  display: flex;
  align-items: center;
  padding: 5px;
  border-radius: 5px;
}
.monyListItem.active{
background-color: teal;

}
.monyListItemNumber{
  font-size: 18px;
  font-weight: 100;
  width: 30px;
}
.monyListItemAmount{
  font-size: 20px;
  font-weight: 300;
}
.endtext{
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  
}
.start{
  width: 250px;
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  position: relative;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
}
.startplayer{
  width: 100%;
  height: 30px;
  border: none;
  border-radius: 5px;
  text-align: center;
  font-size: 18px;
}
.startplayer:focus{
  outline: none;
}
.btnstart{
  width: 100%;
  border-radius: 5px;
  height: 30px;
  cursor: pointer;
  font-size:18px;
  font-weight: 500px;
}

